import React, { Component } from 'react';

class webcam extends Component {
    state = { 
        loading : false
     }

    enablingcamera = ()=>{
        this.setState({loading:true});
    }
    disablingcamera = ()=>{
        this.setState({loading:false});
    }

    render() { 
        return ( 
            <>
                {this.state.loading === false? <button type="button" onClick={this.enablingcamera} className="btn btn-success">Start Monitoring</button> :
                <div>
                <img id = "webcam_image" style= {{border: 'solid 5px' , height : '500px'}} src="/video_feed" className="d-inline-block align-top" alt="video_feed"/>
                <button type="button" style = {{position: 'relative' , left: '40%', marginTop: '50px'}} onClick={this.disablingcamera} className="btn btn-danger">Stop Monitoring</button>
                </div>
                }
            </>
         );
    }
}
 
export default webcam;